import {
  HeaderEnd,
  HeaderWrapper,
  HeaderWrapperSX,
} from "Containers/App/LayoutComponents";
import UserAvatar from "Containers/App/UserAvatar";

const DeskBar = () => {
  return (
    <HeaderWrapper sx={HeaderWrapperSX}>
      <HeaderEnd sx={{ ml: "auto" }}>
        <UserAvatar />
      </HeaderEnd>
    </HeaderWrapper>
  );
};

export default DeskBar;
