import { Outlet } from "react-router-dom";

import { useGlobal } from "Contexts/App";
import { BusinessProvider, SettingsProvider } from "Contexts/Business";
import { Content, Wrapper, WrapperSX } from "./LayoutComponents";
import Sidebar from "./Sidebar";
import DeskBar from "./DeskBar";

const BusinessSettingsLayout = () => {
  const { isTablet } = useGlobal();

  return (
    <BusinessProvider>
      <SettingsProvider>
        <Wrapper sx={WrapperSX}>
          {isTablet && <Sidebar />}
          <Content>
            {isTablet && <DeskBar />}
            <Outlet />
          </Content>
        </Wrapper>
      </SettingsProvider>
    </BusinessProvider>
  );
};

export default BusinessSettingsLayout;
